  background-position: center;
  background-size: cover;
background-repeat: no-repeat;
/* min-width: 100%;
min-height: 100%; */
    text-align: -webkit-center;
}

.photo-site{
  background-color: #0276b0;
  width: 535.9px;
    height: 283px;
    border-radius: 15px;

    transform:
rotate3d(.4,-.866,0,05deg)
rotate(2deg);
box-shadow:
2em 4em 6em -2em rgba(0,0,0,.5),
1em 2em 3.5em -2.5em rgba(0,0,0,.5);
transition:
transform .4s ease,
box-shadow .4s ease;
border-radius: .5em;
}
.photo-site:hover{
  transform: scale(2);
  -ms-transform: scale(0.7);
  cursor: pointer;
  transform:
  rotate3d(0,0,0,0deg)
  rotate(0deg)
  scale(1.05);
}



